import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  IStationByBuslineVM,
  StationAddEditVM,
  StationGetResponseVM,
  StationGetVM,
} from "app/dashboard/models/setup/general-setup/location/station";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class StationService {
  constructor(private http: HttpClient) {}

  // Return All  Stations by page
  getAllStations(page: number) {
    return this.http.get<StationGetResponseVM>(
      `${environment.baseUrl}Station/getStationByPage/${page}`
    );
  }

  getAllStationsDropDown() {
    return this.http.get<StationGetVM[]>(
      `${environment.baseUrl}Station/getAllStations`
    );
  }

  // Return All  Stations by streetId
  getAllStationsByStreetId(streetId: number) {
    return this.http.get<StationGetVM[]>(
      `${environment.baseUrl}Station/getAllStationByStreetId/${streetId}`
    );
  }

  // Return All  Stations by busline
  getAllStationsByBuslineId(busline: number) {
    return this.http.get<IStationByBuslineVM[]>(
      `${environment.baseUrl}BusLineDetails/getAllStationByBusId/${busline}`
    );
  }

  //Add - edit New Station
  addEditStation(data: StationAddEditVM) {
    return this.http.post(`${environment.baseUrl}Station/addEditStation`, data);
  }

  //Delete Station
  deleteStation(id: number) {
    return this.http.delete(`${environment.baseUrl}Station/deleteStation/${id}`);
  }
}
