import { Observable } from "rxjs";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  ChronicDiseaseAddEditVM,
  ChronicDiseaseGetResponseVM,
  ChronicDiseaseGetVM,
} from "app/dashboard/models/setup/general-setup/chronic-disease";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class ChronicDiseaseService {
  constructor(private http: HttpClient) {}

  // Return All  ChronicDiseases by page
  getAllChronicDiseases(page: number,EduCompId:number): Observable<ChronicDiseaseGetResponseVM> {
    return this.http.get<ChronicDiseaseGetResponseVM>(
      `${environment.baseUrl}ChronicDisease/getAllChronicDiseaseByEduCompId/${EduCompId}/${page}`
    );
  }

  // Return All  ChronicDiseases > Drop down list
  getAllChronicDiseasesDropDown(EduCompId:number): Observable<ChronicDiseaseGetVM[]> {
    return this.http.get<ChronicDiseaseGetVM[]>(
      `${environment.baseUrl}ChronicDisease/getAllChronicDisease/${EduCompId}`
    );
  }

  //Add and Edit New
  addEditChronicDisease(data: ChronicDiseaseAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}ChronicDisease/addEditChronicDisease`,
      data
    );
  }



  //Delete ChronicDisease
  deleteChronicDisease(id: number) {
    return this.http.delete(
      `${environment.baseUrl}ChronicDisease/deleteChronicDisease/${id}`
    );
  }
}
