import { Observable } from "rxjs";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  VaccinationAddEditVM,
  VaccinationGetResponseVM,
  VaccinationGetVM,
} from "app/dashboard/models/setup/general-setup/vaccination";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class VaccinationService {
  constructor(private http: HttpClient) {}

  // Return All  Vaccinations by page
  getAllVaccinations(page: number,EduCompId:number): Observable<VaccinationGetResponseVM> {
    return this.http.get<VaccinationGetResponseVM>(
      `${environment.baseUrl}Vacination/getVacinationByPage/${page}`
    );
  }

  // Return All  Vaccinations
  getAllVaccinationsDropDown(EduCompId:number): Observable<VaccinationGetVM[]> {
    return this.http.get<VaccinationGetVM[]>(
      `${environment.baseUrl}Vacination/getAllVacination`
    );
  }

  //Add and Edit Vaccination
  addEditVaccination(data: VaccinationAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}Vacination/addEditVacination`,
      data
    );
  }


  //Delete Vaccination
  deleteVaccination(id: number) {
    return this.http.delete(
      `${environment.baseUrl}Vacination/deleteVacination/${id}`
    );
  }
}
