import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import { IAddEditBusLine } from "app/dashboard/models/setup/general-setup/bus-line";
@Injectable({
  providedIn: "root",
})
export class BusLineService {
  constructor(private http: HttpClient) {}
  // Return All Bus Lines
  getBusLines(page: number,EduCompId:number) {
    console.log("ed",EduCompId);

    return this.http.get(
      `${environment.baseUrl}BusLine/getBusLineByeduCompId/${EduCompId}/${page}`
    );
  }

  // Return All Bus Lines
  getBusLinesDropDown(EduCompId:number) {
    return this.http.get(
      `${environment.baseUrl}BusLine/getAllBusLine/${EduCompId}`
    );
  }

  // Return All Bus Lines by Area id
  getBusLinesDropDownByAreaId(AreaId: number,EduCompId:number) {
    return this.http.get(
      `${environment.baseUrl}BusLine/getBusLineByAreaId/${EduCompId}/${AreaId}`
    );
  }
  //Add and Edit New Bus Line
  addEditBusLine(data: IAddEditBusLine) {
    return this.http.post(
      `${environment.baseUrl}BusLine/addEditBusLine`,
      data
    );
  }


  //Delete Bus Line
  deleteBusLine(BusLineId: number) {
    return this.http.delete(
      `${environment.baseUrl}BusLine/deleteBusLine/${BusLineId}`
    );
  }
}
